$black: #232526;
$blue: #2351ff;
$red: #f53a3a;

.layout {
 &-child {
  margin-top: 20px;
 }
}

.header {
 &__top {
  padding: 20px 60px 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
 }
}

.menu {
 &-list {
  display: flex;
  align-items: center;

  &__item {
   & + .menu-list__item {
    margin-left: 40px;
   }

   img {
    width: 12px;
    margin-left: 8px;
    margin-bottom: 3px;
   }
  }
 }
}

.user {
 display: flex;
 align-items: center;
 position: relative;
 cursor: pointer;

 &__logo {
  margin-right: 60px;
 }

 &__name {
  margin-right: 20px;
 }

 &-menu {
  position: absolute;
  top: 60px;
  right: 0;
  z-index: 5;

  &__lists {
   width: 190px;
   background: #fff;
   box-shadow: 0 4px 14px 2px rgba(35, 81, 255, 0.06);
   border-radius: 20px;
   padding: 25px 58px 25px 33px;

   &-list {
    font-size: 18px;
    line-height: 35px;
   }
  }
 }
}

.footer {
 background-color: $blue;
 color: #fff;
 padding: 0 60px 40px;
 position: relative;

 &:before {
  content: '';
  width: 240px;
  height: 100%;
  background-color: $blue;
  position: absolute;
  left: -240px;
  top: 0;
 }

 &:after {
  content: '';
  width: 240px;
  height: 100%;
  background-color: $blue;
  position: absolute;
  right: -240px;
  top: 0;
 }

 a {
  color: #fff;
 }

 &__top {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-bottom: 100px;
  margin-top: 50px;

  &-social {
   &__text {
    margin-bottom: 18px;
   }

   &__links {
    margin-right: 140px;
    display: flex;

    &-link {
     & + .footer__top-social__links-link {
      margin-left: 15px;
     }
    }
   }
  }
 }

 &__info {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &-inner {
   display: flex;

   &__terms {
    margin-left: 40px;
   }
  }
 }
}

.authentication {
 .ant-btn {
  margin-right: 30px;
  padding: 13px 28px !important;
  font-size: 18px;
  line-height: 23px;
 }
}

.slider-switch {

 &__dots {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 40px;
  &-dot {
   padding: 0;
   margin-right: 15px;
   width: 7px;
   height: 7px;
   background-color: #b9cbfe;
   border: 1px solid #b9cbfe;
   border-radius: 50%;
   cursor: pointer;
   &.active {
    width: 10px;
    height: 10px;
    background-color: #2351FF;
   }
  }
 }

 .ant-switch {
  max-width: 1440px;
  width: 100%;
  height: 780px;
  border-radius: 720px;
  transition: all 1.5s ease;
  background-image: url('../../../common/images/main/slide-1.png');
  background-repeat: no-repeat;
  background-size: cover;
 }
 .ant-switch-handle {
  top: 1px;
  width: 780px;
  height: 778px;
  transition: all 1.5s ease;
  &:before {
   transition: all 1.5s ease;
   background-image: url('../../../common/images/main/slide-1-circle.png');
   background-repeat: no-repeat;
   background-size: cover;
  }
 }
 .ant-switch-checked .ant-switch-handle {
  left: calc(100% - 782px);

  &:before {
   background-image: url('../../../common/images/main/slide-2-circle.png');
   background-repeat: no-repeat;
   background-size: cover;
  }
 }

 .ant-switch-checked {
  transition: all 1.5s ease;
  background-image: url('../../../common/images/main/slide-2.png');
  background-repeat: no-repeat;
  background-size: cover;
 }
}

.burg-menu {
 position: relative;
 z-index: 6;

 &__inner {
  margin-top: 50px;
  padding-top: 70px;
  padding-left: 100px;
  width: 100%;
  height: 100vh;
  background-color: #FAFBFE;
  position: absolute;
  z-index: 6;
  right: -22px;

  .menu {

   &-list {
    display: block;

    &__item {
     &:last-child {
      margin-top: 80px;
      font-size: 18px;
      line-height: 30px;

      a {
       position: relative;
       color: #a3a8bb;

       &:before {
        content: '';
        width: 14px;
        height: 14px;
        position: absolute;
        right: -24px;
        top: 5.5px;
        background-image: url('../../../common/images/main/arrow-right-gray.svg');
        z-index: 8;
       }
      }
     }

     & + .menu-list__item {
      margin-left: 0;
     }
    }
   }
  }
 }

 .user {
  &__name {
   font-size: 18px;
   line-height: 23px;
   margin-left: 18px;
   position: relative;

   &:before {
    content: '';
    width: 14px;
    height: 14px;
    position: absolute;
    right: -24px;
    top: 5.5px;
    background-image: url('../../../common/images/auth/arrow-right.svg');
    z-index: 8;
   }
  }
 }

 &__account {
  display: flex;
  align-items: center;
  margin-bottom: 60px;
 }

 &__lists {
  background-color: #FAFBFE;
  box-shadow: none;
  padding: 0;
  max-width: 310px;
  width: 100%;
 }
}

.hide {
 display: none;
}

.ham {
 position: absolute;
 right: -20px;
 z-index: 4;
 top: -18px;
 display: block;
 cursor: pointer;
 transition: transform 400ms;
 user-select: none;

 .top {
  stroke-dasharray: 40 160;
 }

 .middle {
  stroke-dasharray: 40 142;
  transform-origin: 50%;
  transition: transform 400ms;
 }

 .bottom {
  stroke-dasharray: 40 85;
  transform-origin: 50%;
  transition: transform 400ms, stroke-dashoffset 400ms;
 }

 &.active {
  .top {
   stroke-dashoffset: -64px;
  }

  .middle {
   transform: rotate(90deg);
  }

  .bottom {
   stroke-dashoffset: -64px;
  }
 }
}

.hamRotate.active {
 transform: rotate(45deg);
}

.line {
 fill: none;
 transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
 stroke: #2351ff;
 stroke-width: 4;
 stroke-linecap: round;
}

// ________________________________

@media screen and (max-width: 1060px) {
 body {
  font-size: 16px;
  line-height: 20px;
 }
 .footer {
  overflow: hidden;
  &:after {
   width: 0;
   height: 0;
   background: none;
   right: 0;
   top: 0;
  }
 }
}

@media screen and (max-width: 991px) {
 /* стили для больших планшетов - начало */
 .header__top {
  align-items: flex-start;
  position: static;

  &-link {
   img {
    width: 160px;
   }
  }
 }
 .menu-list__item {
  font-size: 24px;
  line-height: 50px;
 }
 .footer {
  padding: 0 0 40px;
  &__top {
   display: block;
   text-align: center;

   &-logo {
    margin-bottom: 40px;
   }

   &-social {
    margin-right: 0 !important;
    margin-bottom: 50px;

    &__links {
     max-width: 200px;
     margin: 0 auto;
    }
   }
  }

  &__info {
   max-width: 440px;
   margin: 0 auto;
   flex-wrap: wrap;
   justify-content: center;
   flex-direction: column-reverse;
   font-size: 14px;

   &-inner {
    margin-right: 0 !important;
    margin-top: 10px;
    margin-bottom: 10px;

    &__terms {
     margin-left: 20px;
    }
   }
  }
 }
}

@media screen and (max-width: 767px) {
 /* стили для средних планшетов - начало */
 .header__top {
  padding: 20px 40px 40px;

  &-link {
   img {
    width: 140px;
   }
  }
 }
 .burg-menu__inner {
  padding-left: 60px;
 }
}

@media screen and (max-width: 479px) {
 /* стили для телефонов - начало */

 .header__top {
  padding: 20px 20px 40px;

  &-link {
   img {
    width: 120px;
   }
  }
 }
 .burg-menu__inner {
  padding-left: 20px;
 }
}
